import React, { useEffect, useRef } from "react";
import { useFrame, useThree } from '@react-three/fiber';
//import { useMediaQuery } from "@material-ui/core";
import {calculateScrollOffset, lerp} from "../CustomMath";

const MousePointLight3D = ({ zPosition, intensity, decay }) => {
    const lightRef = useRef();
    const { mouse } = useThree();
    let scrollOffset = 0;

    /*
    const isTouchscreen = useMediaQuery('(hover: none)');

    useEffect(() => {
        
        if(isTouchscreen) {
            const handleTouchStart = (event) => {
                console.log(event.touches[0]);
                const touch = event.touches[0];
                const touchX = touch.clientX;
                const touchY = touch.clientY;

                console.log(mouse.x, mouse.y, touchX, touchY);
            
                // Update mouse position with touch coordinates
                //mouse.x = touchX; //(touchX / window.innerWidth) * 2 - 1;
                //mouse.y = touchY; // -(touchY / window.innerHeight) * 2 + 1;
            };
        
            window.addEventListener('touchstart', handleTouchStart);
        
            return () => {
            window.removeEventListener('touchstart', handleTouchStart);
            };
        }
    }, [isTouchscreen, mouse]);
    */
   
    const { gl } = useThree();
    const canvasRef = useRef();

    useEffect(() => {
        canvasRef.current = gl.domElement;
        // console.log(canvasRef.current);
    }, [gl]);

    useFrame(() => {
        if (lightRef.current && canvasRef.current) {
            scrollOffset = calculateScrollOffset(canvasRef.current.offsetTop, -200);
            
            const targetX = (mouse.x * window.innerWidth) * 1.5 / 100 / 3 ;
            const targetY = (mouse.y * window.innerHeight) * 1.5 / 100 / 2;

            lightRef.current.position.x = lerp(lightRef.current.position.x, targetX, 0.015);
            lightRef.current.position.y = lerp(lightRef.current.position.y, targetY, 0.015);
            lightRef.current.position.z = zPosition + lightRef.current.position.x ;

            // Position scroll animation
            const xPositionValue = lightRef.current.position.x + scrollOffset / 40;
            const zPositionValue = lightRef.current.position.z - scrollOffset * 4;
            lightRef.current.position.x = xPositionValue;
            lightRef.current.position.z = zPositionValue; 
            
        }
        //console.log(lightRef.current.position);
    });

    return <pointLight ref={lightRef} intensity={intensity} decay={decay} />;
};

export default MousePointLight3D;