import React, { useContext, useEffect } from "react";
import { Typography, Button, Icon, makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import { ArrowDownward } from "@material-ui/icons";
import { motion, useAnimation } from "framer-motion";
import { Link } from "react-scroll";
import HomeContainer from "../../containers/HomeContainer";
import { useTranslation } from "react-i18next";
import loaderContext from "../../contexts/loaderContext";

const Home = () => {
    const classes = useStyles();
    const theme = useTheme();
    const { isLoading } = useContext(loaderContext);
    const controls = useAnimation();
    const { t } = useTranslation();
    const isMobileOrTablet = useMediaQuery(theme.breakpoints.down("md"));

    useEffect(() => {
        if (!isLoading) {
            controls.start((i) => ({
                y: 0,
                opacity: 1,
                transition: { delay: i * 0.1 + 1.2 },
            }));
        } else {
            controls.start({ opacity: 0, y: 5 });
        }
    }, [isLoading, controls]);

    return (
        <HomeContainer id="home">
            <div className={classes.homeContent}>
                <Typography
                    component={motion.div}
                    animate={controls}
                    custom={0}
                    color="primary"
                    variant="h5"
                    className={`${classes.shadow} ${classes.onTop}`}
                >
                    
                        {t("home_welcome")}
                </Typography>
                <Typography
                    component={motion.p}
                    animate={controls}
                    custom={1}
                    variant="h2"
                    color="primary"
                    className={`${classes.subTitle} ${classes.shadow} ${classes.onTop}`}
                >
                    {t("home_what_i_do")}
                </Typography>
                <Typography
                    component={motion.p}
                    animate={controls}
                    custom={2}
                    variant="body2"
                    className={`${classes.summary} ${classes.shadow} ${classes.onTop}`}
                >
                    {t("home_job")}
                </Typography>
                <motion.div animate={controls} custom={3}>
                    <Button
                        component={Link}
                        spy
                        smooth
                        offset={0}
                        duration={500}
                        to="about"
                        variant="outlined"
                        color="primary"
                        size="large"
                        className={`${classes.shadow} ${classes.onTop}`}
                    >
                        {t("home_contact_btn")}
                    </Button>
                    {isMobileOrTablet && (
                        <motion.div>
                            <Icon
                                component={motion.div}
                                transition={{ delay: 0.5, repeat: Infinity, duration: 0.5, repeatType: "loop", repeatDelay: 2.0, ease: "easeInOut" }}
                                variants={{ hover: { y: -50 }, initial: { y: 150 } }}
                                animate="hover"
                                className={`${classes.homeArrowMobile} ${classes.shadow} ${classes.onTop}`}
                            >
                                <ArrowDownward />
                            </Icon>
                            <div className={`${classes.homeArrowMobileSpacing}`}></div>
                        </motion.div>
                    )}
                </motion.div>
                {!isMobileOrTablet && (
                    <motion.div animate={controls} custom={4}>
                        <Icon
                            component={motion.div}
                            transition={{ delay: 0.0, repeat: Infinity, duration: 1.0, repeatType: "loop", repeatDelay: 2.0, ease: "easeOut" }}
                            variants={{ hover: { y: -50 }, initial: { y: 1 } }}
                            animate="hover"
                            className={`${classes.homeArrow} ${classes.shadow} ${classes.onTop}`}
                        >
                            <ArrowDownward />
                        </Icon>
                    </motion.div>
                )}
            </div>
        </HomeContainer>
    );
};

const useStyles = makeStyles((theme) => ({
    subTitle: {
        marginBottom: "16px",
        fontSize: "75px",
        [theme.breakpoints.down("sm")]: {
            fontSize: "45px",
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "35px",
        },
    },
    summary: {
        marginBottom: "30px",
        color: theme.palette.text.secondary,
        [theme.breakpoints.down("sm")]: {
            fontSize: "16px",
            color: theme.palette.text.tertiary,
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "14px",
        },
    },
    homeContent: {
        display: "inline-block",
        width: "100%",
    },
    homeArrow: {
        display: "inline-block",
        width: "100%",
        marginLeft: "auto",
        marginRight: "auto",
        marginTop: "20vh",
        textAlign: "center",
        justifyContent: "center",
    },
    homeArrowMobile: {
        display: "inline-block",
        width: "100%",
        marginTop: "2vh",
        marginLeft: "auto",
        marginRight: "auto",
        textAlign: "center",
        justifyContent: "center",
        paddingBottom: "20vh",
    },
    homeArrowMobileSpacing: {
        marginBottom: "12vh",
    },
    onTop: {
        position: "relative",
        zIndex: 1, // make sure it is in front of the three canvas
    },
    shadow: {
        textShadow: `0px 0 12px ${theme.palette.background.default},
                     0px 0 8px ${theme.palette.background.default},
                     0px 0 4px ${theme.palette.background.default},
                     0px 0 2px ${theme.palette.background.default},
                     0px 0 1px ${theme.palette.background.default}`,
    },
}));

export default Home;
