import React, { useEffect, useContext } from "react";
import { motion, useAnimation } from "framer-motion";
import loaderContext from "../../contexts/loaderContext";
import ThemeContext from "../../contexts/themeContext";
import { makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import { MeshPhysicalMaterial, Color } from 'three';
import { Canvas } from '@react-three/fiber';
import MousePointLight3D from "../Asset3D/MousePointLight3D";
import { Asset3D, AssetType } from "../Asset3D/Asset3D";

const HomeBanner = () => {
    const classes = useStyles();
    const { isLoading } = useContext(loaderContext);
    const controls = useAnimation();
    const { isDarkMode } = useContext(ThemeContext);
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
    //const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

    let settings = {};
    settings.lights = {};

    if(isDarkMode) {
        settings.material = new MeshPhysicalMaterial({
            reflectivity: 0,
            transmission: 0.0,
            roughness: 0.6,
            metalness: 0.2,
            clearcoat: 0.3,
            clearcoatRoughness: 0.3,
            color: new Color(0xffffff),
            ior: 1.2,
            thickness: 10.0,
        });
        settings.lights.mousePoint = {
            intensity: 8,
            decay: 2.4,
        };

    } else {
        settings.material = new MeshPhysicalMaterial({
            color: new Color(0xCBAA36), // Gold color
            metalness: 0.2, // High metalness for a metallic look
            roughness: 0.9, // Low roughness for a shiny surface
            reflectivity: 1.0, // High reflectivity
            clearcoat: 0.6, // Clearcoat for additional shine
            clearcoatRoughness: 0.1, // Low clearcoat roughness
            transmission: 0.0, // No transmission for an opaque material
            ior: 1.5, // Index of refraction
        });
        settings.lights.mousePoint = {
            intensity: 20,
            decay: 2.2,
        };
    }

    if(isMobile) {
        settings.transform = {
            position: [0.05, -6.5, -11],
            rotation: [0, 85, 0],
            scale: 0.06,
            rotationFactor: Math.PI / 2,
        };
        settings.lights.mousePoint.zPosition = -8;
        settings.lights.spot = {
            position: [-10, 10, -16],
            angle: Math.PI / 1.5,
        };
        settings.lights.directional = {
            position: [-15, 10, -6],
        };
    } else {
        settings.transform = {
            position: [5, -5, -11],
            rotation: [0, 85, 0],
            scale: 0.1,
            rotationFactor: Math.PI / 2.2, // scrollRotationFactor = Math.PI * 2 for full scroll
        };
        settings.lights.mousePoint.zPosition = -13;
        settings.lights.spot = {
            position: [-5, 10, -16],
            angle: Math.PI / 1.5,
        };
        settings.lights.directional = {
            position: [-10, 10, -6],
        };
    }

    useEffect(() => {
        if (!isLoading) {
            controls.start({
                opacity: 1,
                transition: {
                    delay: 3,
                    type: "spring",
                    stiffness: 260,
                    damping: 20,
                },
            });
            // TODO: trigger initial 3D animation

        } else {
            controls.start({ opacity: 0 });
        }
    }, [isLoading, controls]);

    return (
        <motion.div animate={controls} className={classes.wrapper}>
            
            <div animate={controls} className={classes.container}>
                
                {!isLoading && (
                    <Canvas camera={{ position: [0, 0, 0] }} >
                        
                        {isDarkMode === false && (
                            <ambientLight intensity={0.5} castShadow/>
                        )}
                        {isDarkMode === false && (
                            <directionalLight position={settings.lights.directional.position} intensity={3} castShadow />
                        )}
                        <spotLight position={settings.lights.spot.position} angle={settings.lights.spot.angle} intensity={10} penumbra={0} decay={1.4} castShadow/> 
                    
                        <MousePointLight3D zPosition={settings.lights.mousePoint.zPosition} intensity={settings.lights.mousePoint.intensity} decay={settings.lights.mousePoint.decay}/>
                        
                        <Asset3D type={AssetType.Home} position={settings.transform.position} rotation={settings.transform.rotation} scale={settings.transform.scale} scrollRotationFactor={settings.transform.rotationFactor} material={settings.material}/>
                        
                    </Canvas>
                )}
                
                <div className={classes.canvasOverlay}></div>
            </div>
        </motion.div>
    );
};

const useStyles = makeStyles((theme) => ({
    wrapper: {
        position: "absolute",
        top: 0,
        right: 0,
        width: "100%",
        zIndex: 0,
    },
    container: {
        position: "relative",
        height: "100vh",
        width: "100%",
        pointerEvents: "auto",
    },
    canvasOverlay: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        pointerEvents: "none", /* Ensure the overlay doesn't block interactions with the canvas */
        boxShadow: `0 0 50px 50px ${theme.palette.background.default} inset`,
        [theme.breakpoints.down("md")]: {
            boxShadow: `0 0 30px 30px ${theme.palette.background.default} inset`,
        },
        [theme.breakpoints.down("sm")]: {
            boxShadow: `0 0 20px 20px ${theme.palette.background.default} inset`,
        },
    },
    hideLogo: {
        position: "absolute",
        right: 0,
        bottom: 0,
        width: "100px",
        height: "100px",
        backgroundColor: theme.palette.background.default,
    },
}));

export default HomeBanner;
