import React from "react";
import { useTheme, Grid, Typography, makeStyles, Box, useMediaQuery } from "@material-ui/core";
import SectionContainer from "../../containers/SectionContainer";
import { Asset3D, AssetType } from "../../components/Asset3D/Asset3D";
import { useTranslation } from "react-i18next";

const About = () => {
    const classes = useStyles();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
    const { t } = useTranslation();
    return (
        <SectionContainer id="about" title={t("menu_about")} className={classes.aboutContainer}>
            <Grid container spacing={0} alignItems="center" className={classes.aboutGrid}>
                {isMobile && (
                    <Grid item xs={12} md={5} className={classes.gridItemWrapper}>
                        <div className={classes.profilePictureWrapper}>
                            <Asset3D type={AssetType.Profile} />
                        </div>
                    </Grid>
                )}
                <Grid
                    item
                    xs={12}
                    md={7}
                    className={classes.gridItemWrapper}
                    style={{ flexDirection: "column", alignItems: "space-around" }}
                >
                    <Box mb={4}>
                        <Typography variant="body1" className={classes.aboutText}>{t('about_desc_1')}</Typography>
                        <Typography variant="body1" className={classes.aboutText}>{t('about_desc_2')}</Typography>
                    </Box>
                    {/*<Skills />*/}
                </Grid>
                {!isMobile && (
                    <Grid item xs={12} md={5} className={classes.gridItemWrapper}>
                        <div className={classes.profilePictureWrapper}>
                            <Asset3D type={AssetType.Profile} />
                        </div>
                    </Grid>
                )}
            </Grid>
        </SectionContainer>
    );
};

const useStyles = makeStyles((theme) => ({
    aboutContainer: {
        paddingBottom: "0px !important",
        maxWidth: theme.breakpoints.values.xl,
        minHeight: "80vh",
    },
    aboutGrid: {
        width: "100%",
        minHeight: "80vh",
    },
    aboutText: {
        textAlign: "justify",
        textJustify: "inter-word",
        [theme.breakpoints.down("sm")]: {
            fontSize: "16px",
            marginLeft: "3vw",
            marginRight: "3vw",
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "14px",
        },
    },
    gridItemWrapper: {
        justifyContent: "center",
        alignItems: "center",
    },
    profilePictureWrapper: {
        margin: "auto",
        width:"90%",
    },
}));

export default About;
